import React, { useMemo } from "react";
import { useLocation } from "@reach/router";
import { QRCodeCanvas } from "qrcode.react";
import "../../../../componets/log-in/login.css";
import { events } from "../../../../content/events";


const i18n = events.test;

const TicketViewPage: React.FC = () => {
  const location = useLocation();

  // Extract ticketId from URL
  const ticketId = useMemo(() => {
    const parts = (location?.pathname || "")
      .replace(/\/+$/, "")
      .split("/");
    return parts[parts.length - 1] || undefined;
  }, [location?.pathname]);

  const language: "EN" | "VN" = "EN";
  const t = i18n[language];

  // Build QR URL
  const qrURL = ticketId
    ? `https://tinhhoaviet.org.vn/event/aid/admissions/${ticketId}/`
    : null;

  return (
    <div className="auth-shell">
      {/* LEFT SIDE */}
      <aside className="brand-side">
        <div className="brand-sheen" />
        <div className="brand-pattern" />

        <div className="brand-wrap">
          <h1 className="brand-name">{t.brandTitle}</h1>
          <p className="brand-tag">{t.tagline}</p>
        </div>
      </aside>

      {/* RIGHT SIDE */}
      <main className="form-side">
        <section className="form-wrap" style={{ color: "var(--off-white)" }}>
          {!ticketId && (
            <p style={{ color: "var(--gold-2)" }}>
              Ticket ID missing from URL.
            </p>
          )}

          {ticketId && (
            <>
              <h2
                className="title"
                style={{
                  textAlign: "center",
                  marginBottom: 20,
                  color: "var(--off-white)",
                }}
              >
                Your Ticket
              </h2>

              <p
                style={{
                  textAlign: "center",
                  color: "var(--light-gray)",
                  marginBottom: 20,
                }}
              >
                Ticket ID: <strong>{ticketId}</strong>
              </p>

              {/* QR CODE */}
              <div
                style={{
                  display: "flex",
                  justifyContent: "center",
                  marginTop: 10,
                }}
              >
                <QRCodeCanvas
                  value={qrURL!}
                  size={260}
                  includeMargin={true}
                />
              </div>

              <p
                style={{
                  marginTop: 15,
                  textAlign: "center",
                  color: "var(--gold-2)",
                }}
              >
                Scan to verify entry.
              </p>
            </>
          )}
        </section>
      </main>
    </div>
  );
};

export default TicketViewPage;
